import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { 
  MatListModule,
  MatIconModule,
  MatButtonModule,
  MatCardModule,
  MatMenuModule,
  MatSlideToggleModule,
  MatGridListModule,
  MatChipsModule,
  MatCheckboxModule,
  MatRadioModule,
  MatTabsModule,
  MatInputModule,
  MatProgressBarModule,
  MatExpansionModule,
  MatSelectModule,
  MatDatepickerModule,
  MatNativeDateModule
 } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';
import { SharedModule } from './../../shared/shared.module';
import { Ng5SliderModule } from 'ng5-slider';
import {SlideshowModule} from 'ng-simple-slideshow';

import { AppLoaderModule } from '../../shared/services/app-loader/app-loader.module';


import { AppBlankComponent } from './app-blank/app-blank.component';
import { AppCatalogComponent } from '../catalog/app-catalog.component';
import { AppArtistComponent } from '../artist/app-artist.component';
import { AppSongsComponent } from '../songs/app-songs.component';
import { AppPlaylistsComponent } from '../playlists/app-playlists.component';
import { AppPlaylistComponent } from '../playlist/app-playlist.component'
import { AppNewsComponent } from '../news/app-news.component';
import { AppNewsDetailComponent } from '../newsdetail/app-newsdetail.component';
import { AppAboutComponent } from '../about/app-about.component';
import { AppHomeComponent } from '../home/home.component';
import { AppContactComponent } from '../contact/app-contact.component';
import { AppLicenseRequestComponent } from '../licenserequest/licenserequest.component';
import { AppPlacementComponent } from '../placement/placement.component';
import { PlacementdetailComponent } from '../placementdetail/placementdetail.component';
import { AppMusicReviewComponent } from '../musicreview/musicreview.component';
// import { PlaylistTablePopupComponent } from '../../views/admin/playlist/playlistpopup/playlist-table-popup.component';

import { OthersRoutes } from "./others.routing";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    MatListModule,
    MatIconModule,
    MatButtonModule,
    MatCardModule,
    MatMenuModule,
    MatSlideToggleModule,
    MatGridListModule,
    MatChipsModule,
    MatCheckboxModule,
    MatRadioModule,
    MatTabsModule,
    MatInputModule,
    MatProgressBarModule,
    MatExpansionModule,
    MatSelectModule,
    MatDatepickerModule,
    MatNativeDateModule,
    FlexLayoutModule,
    SharedModule,
    RouterModule.forChild(OthersRoutes),
    Ng5SliderModule,
    SlideshowModule,
    AppLoaderModule
  ],
  declarations: [
    AppBlankComponent,
    AppCatalogComponent,
    AppArtistComponent,
    AppSongsComponent,
    AppPlaylistsComponent,
    AppPlaylistComponent,
    AppNewsComponent,
    AppNewsDetailComponent,
    AppAboutComponent,
    AppHomeComponent,
    AppContactComponent,
    AppLicenseRequestComponent,
    AppPlacementComponent,
    PlacementdetailComponent,
    AppMusicReviewComponent,
    // PlaylistTablePopupComponent
  ],
  entryComponents: [
    // PlaylistTablePopupComponent
  ]
})
export class OthersModule { }



