import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {DomSanitizer} from '@angular/platform-browser';

import { ArtistService } from '../../shared/services/admin/artists.service';
import {environment} from '../../../environments/environment';

@Component({
  selector: 'app-artist',
  templateUrl: './app-artist.component.html',
  styleUrls: ['./app-artist.component.scss']
})
export class AppArtistComponent implements OnInit {

  wavePlayer;
  artist_id;
  artist;
  artistsharelink;
  spotify_embed;

  constructor(
    private activedRoute: ActivatedRoute,
    private artististService: ArtistService,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit() {
    this.artist_id = this.activedRoute.snapshot.params['artist_id'];
    this.loadArtist();
  }

  loadArtist() {
    this.artististService.getArtist(this.artist_id).subscribe(artist => {
      this.artist = artist;
      this.artistsharelink = environment.siteURL + 'page/playlist/' + this.artist.artist_id;
      // console.log(artist.artist_spotify_embed)
      this.spotify_embed = this.sanitizer.bypassSecurityTrustHtml(artist['artist_spotify_embed']);
    })
  }

  gotoLink(url) {
    window.open(url, "_blank"); 
  }
}



