import { Component, OnInit } from '@angular/core';

import { NewsService } from '../../shared/services/admin/news.service';

@Component({
  selector: 'app-news',
  templateUrl: './app-news.component.html',
  styleUrls: ['./app-news.component.css']
})
export class AppNewsComponent implements OnInit {

  news : any[];

  constructor(
    private newsService: NewsService
  ) { }

  ngOnInit() {
    this.loadNews();
  }

  loadNews() {
    this.newsService.getPublished().subscribe((news: any) => {
      this.news = news;
    })
  }

}



