import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { PlacementService } from '../../shared/services/admin/placement.service';

@Component({
  selector: 'app-placement',
  templateUrl: './placement.component.html',
  styleUrls: ['./placement.component.scss']
})
export class AppPlacementComponent implements OnInit {

  placements;

  constructor(
    private placementService: PlacementService,
    private router: Router
  ) { }

  ngOnInit() {
    this.loadPlacements()
  }

  loadPlacements() {
    this.placementService.getAll().subscribe(data => {
      this.placements = data;
      console.log(data)
    })
  }

  openLink(link) {
    if(link) {
      window.open(link)
    }
  }

  gotoartist(artist_id) {
    this.router.navigate(['/page/artist/' + artist_id]);
  }

  gotodetail(placement_id) {
    this.router.navigate(['/page/placement_detail/' + placement_id]);
  }
}



