import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {DomSanitizer} from '@angular/platform-browser';

import { PlacementService } from '../../shared/services/admin/placement.service';
import {environment} from '../../../environments/environment';

@Component({
  selector: 'app-placementdetail',
  templateUrl: './placementdetail.component.html',
  styleUrls: ['./placementdetail.component.scss']
})
export class PlacementdetailComponent implements OnInit {
  placement_id;
  placement: any;
  placementsharelink;
  spotify_embed;

  constructor(
    private activedRoute: ActivatedRoute,
    private placementService: PlacementService,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit() {
    this.placement_id = this.activedRoute.snapshot.params['placement_id'];
    this.loadPlacement();
  }

  loadPlacement() {
    this.placementService.getItem(this.placement_id).subscribe(data => {
      this.placement = data;
      this.placementsharelink = environment.siteURL + 'page/placement_detail/' + this.placement.placement_id;
      this.spotify_embed = this.sanitizer.bypassSecurityTrustHtml(data['placement_spotify_embed']);
    })
  }

}



