import { Component, OnInit } from '@angular/core';

import { DashboardService } from '../../shared/services/admin/dashboard.service'

@Component({
  selector: 'app-musicreview',
  templateUrl: './musicreview.component.html',
  styleUrls: ['./musicreview.component.css']
})
export class AppMusicReviewComponent implements OnInit {

  content;

  constructor(
    private dashboardService: DashboardService
  ) { }

  ngOnInit() {
    this.loadMusicReview();
  }

  loadMusicReview() {
    this.dashboardService.getMusicReview().subscribe((data: any) => {
      this.content = data.musicreview_content;
    })
  }

}



