import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { ArtistService } from '../../shared/services/admin/artists.service';

@Component({
  selector: 'app-catalog',
  templateUrl: './app-catalog.component.html',
  styleUrls: ['./app-catalog.component.scss']
})
export class AppCatalogComponent implements OnInit {
  artists: any[];

  constructor(
    private artististService: ArtistService,
    private router: Router
  ) { }

  ngOnInit() {
    this.getAllArtist();
  }

  getAllArtist() {
    this.artististService.getAll().subscribe(data => {
      this.artists = data;
    })
  }

  gotoartistpage(artist_id) {
    this.router.navigate(['/page/artist/' + artist_id]);
  }

}



