import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';

import { SongsService } from '../../shared/services/admin/songs.service';

@Component({
  selector: 'app-licenserequest',
  templateUrl: './licenserequest.component.html',
  styleUrls: ['./licenserequest.component.scss']
})
export class AppLicenseRequestComponent implements OnInit {

  public itemForm: FormGroup;
  licenseTypes = [
    'Audio Project',
    'Corperate Presentation',
    'Corporate Media',
    'Feature Film',
    'Festival Usage',
    'Independent Film',
    'Live Event',
    'Music Compilation',
    'Non Commercial',
    'Podcast',
    'Public Space',
    'Radio Advertising',
    'Radio Production',
    'Sample/Remix/Covers/Derivative Works',
    'Software',
    'Student Film',
    'TV Advertising',
    'TV Show',
    'Video Game',
    'Video Project',
    'Telephone/Music On Hold',
    'Web Video Clip/Webisode',
    'Website',
    'Other License'
  ];

  terms = [
    '3 Months',
    '6 Months',
    '1 Year',
    '5 Years',
    'Unlimited'
  ];

  musicLengths = [
    '15 Seconds',
    '30 Seconds',
    '60 Seconds',
    'Entire Song'
  ]

  song_id;
  constructor(
    private fb: FormBuilder,
    private activedRoute: ActivatedRoute,
    private songService: SongsService,
    private snack: MatSnackBar
  ) { }

  ngOnInit() {
    this.song_id = this.activedRoute.snapshot.params['song_id'];
    this.buildItemForm()
  }


  buildItemForm() {
    this.itemForm = this.fb.group({
      license_song_id: [this.song_id],
      license_name: ['', Validators.required],
      license_email: ['', [Validators.required, Validators.email]],
      license_phone: ['', [CustomValidators.phone('BD')]],
      license_company: [''],
      license_position: [''],
      license_production_name: [''],
      license_production_budget: [''],
      license_music_budget: [''],
      license_release_date: [],
      license_type: ['Audio Project'],
      license_term: ['3 Months'],
      license_territory: [''],
      license_music_length: ['15 Seconds'],
      license_used_in_credits: ['Yes'],
      license_theme_song: ['Yes'],
      license_context_of_usage: [''],
      license_comments: ['']
    })
  }

  submit() {
    this.songService.licenseRequest(this.itemForm.value).subscribe(data => {
      this.snack.open('Request Sent Successfully!', 'OK', { duration: 5000 });
      this.itemForm.reset();
    })
  }
}



