import { Routes } from '@angular/router';

import { AppHomeComponent } from '../home/home.component';
import { AppCatalogComponent } from '../catalog/app-catalog.component';
import { AppArtistComponent } from '../artist/app-artist.component';
import { AppSongsComponent } from '../songs/app-songs.component';
import { AppPlaylistsComponent } from '../playlists/app-playlists.component';
import { AppPlaylistComponent } from '../playlist/app-playlist.component';
import { AppNewsComponent } from '../news/app-news.component';
import { AppNewsDetailComponent } from '../newsdetail/app-newsdetail.component';
import { AppAboutComponent } from '../about/app-about.component';
import { AppContactComponent } from '../contact/app-contact.component';
import { AppLicenseRequestComponent } from '../licenserequest/licenserequest.component';
import { AppPlacementComponent } from '../placement/placement.component';
import { PlacementdetailComponent } from '../placementdetail/placementdetail.component';
import { AppMusicReviewComponent } from '../musicreview/musicreview.component';

export const OthersRoutes: Routes = [
  {
    path: 'home',
    component: AppHomeComponent,
    data: { title: 'Others', breadcrumb: 'Home'}
  },
  {
    path: 'catalog',
    component: AppCatalogComponent,
    data: { title: 'Others', breadcrumb: 'Artists'}
  },

  {
    path: 'artist/:artist_id',
    component: AppArtistComponent,
    data: { title: 'Others', breadcrumb: 'Artist'}
  },

  {
    path: 'songs',
    component: AppSongsComponent,
    data: { title: 'Songs', breadcrumb: 'Songs'}
  },

  {
    path: 'playlists',
    component: AppPlaylistsComponent,
    data: { title: 'Playlists', breadcrumb: 'Playlists'}
  },

  {
    path: 'playlist/:playlist_id',
    component: AppPlaylistComponent,
    data: { title: 'Playlist', breadcrumb: 'Playlist'}
  },

  {
    path: 'news',
    component: AppNewsComponent,
    data: { title: 'News', breadcrumb: 'News'}
  },

  {
    path: 'newsdetail/:news_id',
    component: AppNewsDetailComponent,
    data: { title: 'News Detail', breadcrumb: 'News Detail'}
  },

  {
    path: 'about',
    component: AppAboutComponent,
    data: { title: 'About', breadcrumb: 'About'}
  },

  {
    path: 'contact',
    component: AppContactComponent,
    data: { title: 'Contact', breadcrumb: 'Contact'}
  },
  
  {
    path: 'licenserequest/:song_id',
    component: AppLicenseRequestComponent,
    data: { title: 'LicenseRequest', breadcrumb: 'LicenseRequest'}
  },
  {
    path: 'placements',
    component: AppPlacementComponent,
    data: { title: 'Placements', breadcrumb: 'Placements'}
  },
  {
    path: 'placement_detail/:placement_id',
    component: PlacementdetailComponent,
    data: { title: 'Placement Detail', breadcrumb: 'Placement Detail'}
  },
  {
    path: 'musicreviews',
    component: AppMusicReviewComponent,
    data: { title: 'MusicReviews', breadcrumb: 'MusicReviews'}
  }

];


