import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { PlaylistService } from '../../shared/services/admin/playlist.service';

@Component({
  selector: 'app-playlist',
  templateUrl: './app-playlists.component.html',
  styleUrls: ['./app-playlists.component.scss']
})
export class AppPlaylistsComponent implements OnInit {

  playlists: any[];
  constructor(
    private playlistService: PlaylistService,
    private router: Router
  ) { }

  ngOnInit() {
    this.loadPlaylists();
  }

  loadPlaylists() {
    this.playlistService.getAll().subscribe((data: any) => {
      this.playlists = data;
    });
  }

  gotoplaylistpage(playlist_id) {
    this.router.navigate(['/page/playlist/' + playlist_id]);
  }

}



