import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { NewsService } from '../../shared/services/admin/news.service';

@Component({
  selector: 'app-newsdetail',
  templateUrl: './app-newsdetail.component.html',
  styleUrls: ['./app-newsdetail.component.scss']
})
export class AppNewsDetailComponent implements OnInit {

  @Input() newsItem: any;
  news_id;

  constructor(
    private activedRoute: ActivatedRoute,
    private newsService: NewsService
  ) { }

  ngOnInit() {
    this.news_id = this.activedRoute.snapshot.params['news_id'];
    this.loadNewsDetail()
  }

  loadNewsDetail() {
    this.newsService.getItem(this.news_id).subscribe(newsItem => {
      this.newsItem = newsItem;
    })
  }

}



