import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {DomSanitizer} from '@angular/platform-browser';

import { PlaylistService } from '../../shared/services/admin/playlist.service';
import {environment} from '../../../environments/environment';

@Component({
  selector: 'app-playlist',
  templateUrl: './app-playlist.component.html',
  styleUrls: ['./app-playlist.component.scss']
})
export class AppPlaylistComponent implements OnInit {

  playlist_id;
  playlist: any;
  playlistsharelink;
  spotify_embed;

  constructor(
    private activedRoute: ActivatedRoute,
    private playlistService: PlaylistService,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit() {
    this.playlist_id = this.activedRoute.snapshot.params['playlist_id'];
    this.loadPlaylist();
  }

  loadPlaylist() {
    this.playlistService.getItem(this.playlist_id).subscribe(data => {
      this.playlist = data;
      this.playlistsharelink = environment.siteURL + 'page/playlist/' + this.playlist.playlist_id;
      this.spotify_embed = this.sanitizer.bypassSecurityTrustHtml(data['playlist_spotify_embed']);
    })
  }

}



