import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { FeaturedService } from '../../shared/services/featured.service';
import { DashboardService } from '../../shared/services/admin/dashboard.service';
import { PlayerService } from '../../shared/services/player.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class AppHomeComponent implements OnInit {

  featuredData;
  sliders;

  constructor(
    private featuredService: FeaturedService,
    private router: Router,
    private dashboardService: DashboardService,
    private playerService: PlayerService
  ) { }

  ngOnInit() {
    this.loadFeaturedData();
    this.loadSliders();
  }

  loadFeaturedData() {
    this.featuredService.getAll().subscribe(data => {
      this.featuredData = data;

      this.featuredData.featured_videos.map(video => {
        var video_id = video.video_link.replace('https://www.youtube.com/watch?v=', '');
        video.video_thumb = 'https://img.youtube.com/vi/' + video_id + '/0.jpg'
      })
    })
  }

  loadSliders() {
    this.dashboardService.getSlidersHome().subscribe(data => {
      this.sliders = data;
    })
  }

  gotoartistpage(artist_id) {
    this.router.navigate(['/page/artist/' + artist_id]);
  }

  gotonews(news_id) {
    this.router.navigate(['/page/newsdetail/' + news_id]);
  }

  gotovideo(video_link) {
    window.open(video_link)
  }

  gotomusicReview() {
    this.router.navigate(['/page/musicreviews']);
  }

  gotoPlaylistPage(playlist_id) {
    this.router.navigate(['/page/playlist/' + playlist_id]);
  }

  gotoSongPage(song) {
    var me = this;
    this.playerService.currentSong = null;
    setTimeout(function() {
      me.playerService.currentSong = song;
    }, 100)
  }
  
  gotoPlacement(placement_id) {
    // this.router.navigate(['/page/playlist/' + playlist_id]);
    this.router.navigate(['/page/placement_detail/' + placement_id]);
  }

}



