import { Component, OnInit } from '@angular/core';
import { Options } from 'ng5-slider';

import { FilterService } from '../../shared/filter.service';
import { SongsService } from '../../shared/services/admin/songs.service';
import { ArtistService } from '../../shared/services/admin/artists.service';
import { PlaylistService } from '../../shared/services/admin/playlist.service';

@Component({
  selector: 'app-songs',
  templateUrl: './app-songs.component.html',
  styleUrls: ['./app-songs.component.scss']
})
export class AppSongsComponent implements OnInit {
  panelOpenState = false;
  vocalsIns = 'all';

  minValueDuration: number = 0;
  maxValueDuration: number = 480;
  optionsDuration: Options = {
    floor: 0,
    ceil: 480,
    step: 5,
    ticksArray: [240],
    translate: (value: number): string => {
      var mins = Math.floor(value / 60);
      var sec = value % 60;
      return mins + ' : ' + sec;
    }
  };

  minValueBPM: number = 0;
  maxValueBPM: number = 300;
  optionsBPM: Options = {
    floor: 0,
    ceil: 300,
    step: 5,
    ticksArray: [150]
  };

  filterOptions;
  isLoadedFilterOptions = false;

  songs;
  filterSongs;
  showSongs;
  songSub;
  artists;
  artistsSub;
  playlists;
  playlistsSub
  
  currentPage = 1;
  songsPerPage = 5;
  searchValue = '';
  filterMoods = [];
  filterGenres = [];
  filterPaces = [];
  filterInsts = [];
  filterKeys = [];
  filterArtists = [];
  filterPlaylists = [];
  filterRIYL = [];
  filterCover;

  constructor(
    private filterService: FilterService,
    private songService: SongsService,
    private artistService: ArtistService,
    private playlistService: PlaylistService) {
  }

  ngOnInit() {
    this.loadFilterOptions();
    this.loadAllSongs();
    this.loadArtists();
    this.loadPlaylists();
  }

  loadFilterOptions() {
    this.filterService.getAll().subscribe(filter_options => {
      this.filterOptions = filter_options;
      this.isLoadedFilterOptions = true;
    })
  }

  loadAllSongs() {
    this.songSub = this.songService.getAll().subscribe(data => {
      this.songs = data;
      this.filter();
    });
  }

  loadArtists() {
    this.artistsSub = this.artistService.getAll().subscribe(data => {
      this.artists = data;
    })
  }

  loadPlaylists() {
    this.playlistsSub = this.playlistService.getAll().subscribe(data => {
      this.playlists = data;
    })
  }

  loadMore() {
    this.currentPage ++;
    this.showSongs = this.filterSongs.slice(0, this.songsPerPage * this.currentPage);
  }

  //mood filer
  selectMood(moodItem) {
    if(this.filterMoods.filter(filter => filter.mood_id == moodItem.mood_id).length) {
      return;
    }
    this.filterMoods.push(moodItem)
    this.filter()
  }

  removeFilterMood(moodItem) {
    this.filterMoods = this.filterMoods.filter(mood => mood.mood_id != moodItem.mood_id)
    this.filter();
  }

  //Genre filter
  selectGenre(genreItem) {
    if(this.filterGenres.filter(filter => filter.genre_id == genreItem.genre_id).length) {
      return;
    }
    this.filterGenres.push(genreItem)
    this.filter()
  }

  removeFilterGenre(genreItem) {
    this.filterGenres = this.filterGenres.filter(genre => genre.genre_id != genreItem.genre_id)
    this.filter();
  }

  //pace filter
  selectPace(paceItem) {
    if(this.filterPaces.filter(filter => filter.pace_id == paceItem.pace_id).length) {
      return;
    }
    this.filterPaces.push(paceItem)
    this.filter()
  }

  removeFilterPace(paceItem) {
    this.filterPaces = this.filterPaces.filter(pace => pace.pace_id != paceItem.pace_id)
    this.filter();
  }

  //Inst filter
  selectInst(instItem) {
    if(this.filterInsts.filter(filter => filter.inst_id == instItem.inst_id).length) {
      return;
    }
    this.filterInsts.push(instItem)
    this.filter()
  }

  removeFilterInst(instItem) {
    this.filterInsts = this.filterInsts.filter(inst => inst.inst_id != instItem.inst_id)
    this.filter();
  }

  //Key filter
  selectKey(keyItem) {
    if(this.filterKeys.filter(filter => filter.key_id == keyItem.key_id).length) {
      return;
    }
    this.filterKeys.push(keyItem)
    this.filter()
  }

  removeFilterKey(keyItem) {
    this.filterKeys = this.filterKeys.filter(key => key.key_id != keyItem.key_id)
    this.filter();
  }

  //Artist filter
  selectArtist(artistItem) {
    if(this.filterArtists.filter(filter => filter.artist_id == artistItem.artist_id).length) {
      return;
    }
    this.filterArtists.push(artistItem)
    this.filter()
  }

  removeFilterArtist(artistItem) {
    this.filterArtists = this.filterArtists.filter(artist => artist.artist_id != artistItem.artist_id)
    this.filter();
  }

   //Playlist filter
   selectPlaylist(playlistItem) {
    if(this.filterPlaylists.filter(filter => filter.playlist_id == playlistItem.playlist_id).length) {
      return;
    }
    this.filterPlaylists.push(playlistItem)
    this.filter()
  }

  removeFilterPlaylist(playlistItem) {
    this.filterPlaylists = this.filterPlaylists.filter(playlist => playlist.playlist_id != playlistItem.playlist_id)
    this.filter();
  }

  selectRIYL(artist) {
    if(this.filterRIYL.filter(filter => filter.artist_id == artist.artist_id).length) {
      return;
    }
    this.filterRIYL.push(artist)
    this.filter()
  }

  removeRIYL(artist) {
    this.filterRIYL = this.filterRIYL.filter(riyl => artist.artist_id != riyl.artist_id)
    this.filter();
  }

  filter() {
    this.currentPage = 1;

    this.filterSongs = this.songs;

    if(this.searchValue) {
      this.filterSongs = this.songs.filter(song => song.song_title && song.song_title.toLowerCase().includes(this.searchValue.toLowerCase()))
    }

    //mood filter
    if(this.filterMoods.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        return this.filterMoods.filter(filter => filter.mood_title == song.song_mood).length
      })
    }

    //genre filter
    if(this.filterGenres.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        return this.filterGenres.filter(filter => filter.genre_title == song.song_genre).length
      })
    }

    //pace filter
    if(this.filterPaces.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        return this.filterPaces.filter(filter => filter.pace_title == song.song_pace).length
      })
    }

    //Inst filter
    if(this.filterInsts.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        return this.filterInsts.filter(filter => filter.inst_title == song.song_instrument).length
      })
    }

    //Key filter
    if(this.filterKeys.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        return this.filterKeys.filter(filter => filter.key_title == song.song_key).length
      })
    }

    //Artist filter
    
    if(this.filterArtists.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        return this.filterArtists.filter(filter => filter.artist_id == song.song_artist.artist_id).length
      })
    }

    //Playlist filter
    // if(this.filterPlaylists.length) {
    //   this.filterSongs = this.filterSongs.filter(song => {
    //     return this.filterPlaylists.filter(filter => filter.playlist_id == song.song_playlist).length
    //   })
    // }

    this.filterSongs = this.filterSongs.filter(song => song.song_duration == null || (parseInt(song.song_duration) >= this.minValueDuration && parseInt(song.song_duration) <= this.maxValueDuration))

    this.filterSongs = this.filterSongs.filter(song => song.song_bpm == null || (parseInt(song.song_bpm) >= this.minValueBPM && parseInt(song.song_bpm) <= this.maxValueBPM))

    if(this.vocalsIns != 'all') {
      this.filterSongs = this.filterSongs.filter(song => song.song_vocals_inst == this.vocalsIns);
    }

    if(this.filterRIYL.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        var riyls = [];
        if(song.song_riyl) {
          riyls = song.song_riyl.split(',');
          var isExist = false;
          this.filterRIYL.map(riyl => {
            if(riyls.includes(riyl.artist_id)) {
              isExist = true
            }
          })
  
          return isExist;
        }
        return false;
      })
    }

    //Artist filter
    
    // if(this.filterPlaylists.length) {
    //   this.filterSongs = this.filterSongs.filter(song => {
    //     return this.filterPlaylists.filter(filter => filter.playlist_id == song.song_playlist).length
    //   })
    // }

    if(this.filterPlaylists.length) {
      this.filterSongs = this.filterSongs.filter(song => {
        var playlists = [];
        if(song.song_playlist) {
          playlists = song.song_playlist.split(',');
          var isExist = false;
          this.filterPlaylists.map(playlist => {
            if(playlists.includes(playlist.playlist_id)) {
              isExist = true
            }
          })
  
          return isExist;
        }
        return false;
      })
    }

    if(this.filterCover) {
      this.filterSongs = this.filterSongs.filter(song => song.song_covers && song.song_covers.toLowerCase().includes(this.filterCover.toLowerCase()))
    }
    
    this.showSongs = this.filterSongs.slice(0, this.songsPerPage * this.currentPage)
    
  }

  durationChange(e) {
    this.filter()
  }

  onVocalChange(event) {
    this.filter()
  }

}



